export default {
	data() {
		return {
			title:"",
			strings:""
		};
	},
	onLoad(e) {
		console.log(e);
		uni.showLoading({
			title: '加载中...',
			mask: false
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				uni.hideLoading();
				console.log('info->',res);
				this.title = res.data.title;
				this.strings = res.data.content;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
